import { Injectable } from '@angular/core';

import { Http,Response } from '@angular/http';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';




@Injectable()
export class InsertService {


  constructor(private _http:Http) {

   }

   saveProfile(name,address){

   		let _url:string='https://curdtest.000webhostapp.com/api/index.php/setuser';
   		return this._http.post(_url,JSON.stringify({name:name,address:address}))
   		.map((res:Response) => res.json()).catch(this._errorHandler);
   }

   private _errorHandler(error:Response){
   console.error("Error Occoured "+ error);
   return Observable.throw(error || 'some error occured');

   }

}



