import { Injectable } from '@angular/core';

import { Http,Response } from '@angular/http';

import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';



@Injectable()
export class DeleteService {

 constructor(private _http:Http) {

   }

   deleteuser(userid){
   		let _url:string='https://curdtest.000webhostapp.com/api/index.php/deleteuser';
   		return this._http.post(_url,JSON.stringify({id:userid}))
   		.map((res:Response) => res.json()).catch(this._errorHandler);
   }

   updateuser(id,name,address){

         let _url:string='https://curdtest.000webhostapp.com/api/index.php/updateuser';
         return this._http.post(_url,JSON.stringify({"id":id,"name":name,"address":address}))
         .map((res:Response) => res.json()).catch(this._errorHandler);
   }


   private _errorHandler(error:Response){
   console.error("Error Occoured "+ error);
   return Observable.throw(error || 'some error occured');

   }



}



