import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { DeleteService } from '../delete.service';

import { Router } from '@angular/router';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css'],
  providers:[DeleteService]
})
export class ViewComponent implements OnInit {


msg:string;
userid:string;
people: any[];



id:string;
name:string;
address:string;

editId:string;
editName:string;
editAddress:string;



 constructor(private httpClient:HttpClient , private deleteService:DeleteService,private router: Router){

			 
       this.httpClient.post('https://curdtest.000webhostapp.com/api/index.php/getuser',JSON.stringify({name:"hello"}))
					  	.subscribe(
					  		(data:any) =>{
					  			this.people=data		  			
					  		},
					        err => console.error(err),
					        () => console.log('done')
					  	)
          



}

  		ngOnInit() {
		   
  		}


    public deleteuser(userid) : void{
      
      this.deleteService.deleteuser(userid).subscribe(res => this.msg = res);

      this.httpClient.post('https://curdtest.000webhostapp.com/api/index.php/getuser',JSON.stringify({name:"hello"}))
					  	.subscribe(
					  		(data:any) =>{
					  			this.people=data		  			
					  		},
					        err => console.error(err),
					        () => console.log('done')
					  	);

               this.router.navigateByUrl('/insert');
   }


   public edituser(id,name,address):void{

   		console.log(id+name+address);

   		this.editName=name;
   		this.editAddress=address;
   		this.editId=id;

   		document.getElementById("editUser").style.display="block";
   		document.getElementById("viewUser").style.display="none";
   }

   public updateData():void{

     this.deleteService.updateuser(this.editId,this.editName,this.editAddress).subscribe(res => this.msg = res);

     this.router.navigateByUrl('/insert');
    
   }


}



